.dataBlock {
    display: block;
    float: right;
    margin-top: 13px;
    margin-right: 6px;
}

.thumbnailInactiv{
    cursor:pointer;
}

.accordionTitel{
    color: white;
    display: inline-block;
    font-size: 1.6rem;
    padding-top: 1.5rem;
    text-decoration: none;
    text-transform: uppercase;
    font-family: Daimler CS Bold, sans-serif;
    font-weight: 700;
    background: #00677f !important;
}

.accordionTitelClosed{
    color: white;
    display: inline-block;
    font-size: 1.6rem;
    padding-top: 1.5rem;
    text-decoration: none;
    text-transform: uppercase;
    font-family: Daimler CS Bold, sans-serif;
    font-weight: 700;
    background: #6c757d  !important;
}

.card {
    margin-bottom: 10px;
}

.nameBlock {
    display: inline-block;
    font-size: 12px;
    margin-left: 5px;
    margin-right: 5px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    width: calc(100% - 60px);
    font-family: Daimler CS Bold, sans-serif;
}

.greenCheck {
    color: #6ea046;
    border: 3px solid #6ea046;
    padding: 3px;
    font-size: 23px;
}

.whiteTxt{
    color: white;
    display: inline-block;
    font-size: 1.6rem;
    text-decoration: none;
    text-transform: uppercase;
    font-family: Daimler CS Bold, sans-serif;
    font-weight: 700;
}


