.abfrageLabel {
color: white;
    text-transform: uppercase;
    font-size: 12px;
    font-family: Daimler CS Bold, sans-serif;
    font-weight: 700;
    margin-top: 10px;
    margin-bottom: 10px;
    width: 100%;
}

.basic-multi-select{
    margin-top: 10px;
    margin-bottom: 10px;
}

.select-info div{

    border: 1px solid black !important;
    box-shadow: none !important;
    border-color: transparent !important;
}

.switch{
    width: 100px;
    margin-top: 10px;
    margin-bottom: 10px;
    height: 39px;
}

.switch-off.btn {
    background: #c8c8c8;
}

.switch-off.btn:hover {
    background: #999;
    color: black;
}

.ratingBlock {
    float: left;
    padding: 3px;
    margin: 5px;
    width: 38px;
    height: 38px;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    font-size: 20px;
    border-radius: 50%;
    background: #003e4c;
    font-weight: 100;
}

.rating {
    border: 2px solid #e69123;
}

.ratingClicked {
    background-color: #e69123;
}